.card-product {
	padding: 0 20px;

	.card-product-img {
		display: block;
		height: 300px;
		max-width: 700px;
		transition: all 0.3s ease-in;
	}

	.card-product-content {
		position: relative;
		transform: translateX(0);
		transition: 0.5s transform;
	}

	&:hover {
		.card-product-img {
			background-color: $secondary;
		}

		.card-product-content {
			transform: translateX(50%);
		    left: -50%;
		    text-align: center;
		}
	}
}

.card-machine {
	&-img {
		height: 493px;
	}
	&-content {
		text-align: center;
	}
}

.card-product,
.card-machine {
	&-img {
		img {
			width: 100%;
			height: 100%;
			object-fit: contain;
			object-position: center;
		}
	}

	&-content {
		@at-root {
			.card-product-title,
			.card-machine-title {
				position: relative;
				color: $body-color;
				display: block;
				font-size: 22px;
				font-family: $font-family-secondary;
				margin-top: 15px;
				text-align: center;
				text-transform: uppercase;
				letter-spacing: -0.22px;
				line-height: 32px;

				@include media-breakpoint-only(sm) {
					font-size: 18px;
					line-height: 26px;
					letter-spacing: -0.18px;
				}

				@include media-breakpoint-down(sm) {
					font-size: 16px;
					line-height: 22px;
					letter-spacing: -0.16px;
				}
			}

			.card-machine-title {
				position: relative;
				padding-left: 30px;
				display: inline-block;

				&:before {
					content: '';
					position: absolute;
					left: 0;
					top: 7px;
					width: 20px;
				    height: 32px;
				    background-image: url('./../img/icons/caret-arrow-up.png');
					background-size: 15px;
				    background-repeat: no-repeat;

				    @include media-breakpoint-up(xxl) {
				    	top: 9px;
				    }

					@include media-breakpoint-down(md) {
						top: 3px;
					}
				}
			}
		}
	}
}

.card-news {
	background-color: $white;
	display: flex;
	align-items: center;

	.card-news-img {
		flex: 0 0 790px;
		overflow: hidden;

		img {
			width: 100%;
			height: 100%;
			object-fit: cover;
			object-position: center;
			transition: all 0.3s ease-in;
		}
	}

	.news-date {
		color: rgba($body-color, 0.8);
		font-family: $font-family-secondary;
		letter-spacing: -0.16px;
		line-height: 28px;
	}

	h2 {
		font-weight: 500;
		color: $body-color;
		margin: 25px 0;
		overflow: hidden;
		text-overflow: ellipsis;
		display: -webkit-box;
		-webkit-line-clamp: 3;
		-webkit-box-orient: vertical;
	}

	p {
		color: rgba($body-color, 0.84);
		font-size: 18px;
		line-height: 30px;
	}

	.link-learn-more {
		font-family: $font-family-secondary;
		font-weight: 500;
		font-size: 20px;
		letter-spacing: -0.2px;
		line-height: 27px;
		font-style: italic;
	}

	.card-news-body {
		padding: 0 160px;
	}

	&.is-small {
		.card-news-img {
			flex: 0 0 260px;
			height: auto;
		}

		.card-news-body {
			padding: 0 60px;
		}

		h2 {
			-webkit-line-clamp: 2;
			font-size: 26px;
			line-height: 33px;
			letter-spacing: -0.26px;
		}
	}

	&:hover {
		.card-news-img {
			img {
				transform: scale(1.1);
			}
		}

		.link-learn-more {
            svg {
                margin-right: 15px;
            }
	    }
	}
}

.product-slider-nav {
	position: absolute;
	top: 115px;
    left: 16px;
    right: 16px;
    display: flex;
    align-items: center;
    justify-content: space-between;

    .slick-arrow {
	    position: relative;
    	display: flex;
    	justify-content: center;
	    background: $primary;
	    width: 60px;
	    height: 60px;
	    // border-radius: 50%;
   		z-index: 1;	
   		cursor: pointer;
	    box-shadow: 0px 12px 32px rgba($black, 0.17);

	    img {
	    	width: 15px;
	    }
	}
}

// Media Queries

@include media-breakpoint-only(xl) {
	.card-news {
		.card-news-img {
			flex: 0 0 674px;
		}
	}
}

@include media-breakpoint-down(xl) {
	.card-product {
		max-width: 400px;

		.card-product-img {
			height: 300px;
		}
	}

	.card-news {
		.card-news-body {
			padding: 20px 60px;
		}

		.card-news-img {
			flex: 0 0 444px;
			height: 528px;
		}
	}

	.card-machine {
		&-img {
			height: 277px;
		}
	}

	.product-slider-nav {
		left: -50px;
    	right: -50px;

		.slick-arrow {
		    width: 45px;
		    height: 45px;

		    img {
		    	width: 10px;
		    }
		}
	}
}

@include media-breakpoint-down(lg) {
	.card-news {
		h2,
		&.is-small h2 {
			font-size: 22px;
			line-height: 30px;
			letter-spacing: -0.22px;
		}

		p {
			font-size: 18px;
		}

		.link-learn-more {
			font-size: 14px;
		}
	}

	.product-slider-nav {
		position: relative;
		top: 0;
		left: auto;
		right: 0;
		justify-content: flex-end;
		margin-bottom: 20px;

		.slick-arrow {
			&:not(:last-child) {
				margin-right: 16px;
			}
		}
	}
}

@include media-breakpoint-down(md) {
	.card-news {
		flex-direction: column;

		.card-news-img {
			height: 300px;
		    flex: 0 0 300px;
		    width: 100%;
		}

		.card-news-body,
		&.is-small .card-news-body {
			padding: 40px 10px;
		}

		&.is-small {
			.card-news-img {
				height: 195px;
			    flex: 0 0 195px;
			}

			p {
				font-size: 16px;
				line-height: 24px;
			}
		}
	}

	.card-product {
		max-width: 350px;

		.card-product-img {
			height: 220px;
		}

		.card-news-body {
			padding: 10px 40px;
		}
	}

	.card-machine-title {
		font-size: 14px;
		line-height: 20px;
		letter-spacing: -0.14px;
	}
}

@include media-breakpoint-down(sm) {
	.card-news {
		.card-news-img {
			height: 195px;
			flex: auto;
		}

		p {
			font-size: 16px;
			line-height: 24px;
		}
	}
}