a {
	transition: all 0.3s ease-in;

	&.link-learn-more {
        svg {
            width: 8px;
            margin-right: 5px;
            vertical-align: revert;
            transition: all 0.3s ease-in;
        }

        &:hover {
            svg {
                margin-right: 15px;
            }
        }
    }
}

.section-title {
	position: relative;
	font-size: 52px;
	letter-spacing: -0.52px;
	line-height: 72px;
	margin: 0;

	&:before {
		content: '';
		position: absolute;
		top: 22px;
		left: -100px;
		display: block;
		width: 23px;
		height: 30px;
		background-color: $primary;
		transform: skewX(-20deg);
	}

	&--secondary {
		&:before {
			background-color: $secondary;
		}
	}

	span {
		color: #5a5a5a;
		display: block;
	}

	@include media-breakpoint-down(xl) {
		&:before {
			left: -90px;
		}
	}

	&.no-before-content {
		&:before {
			display: none;
		}
	}
}

.section-content {
	font-size: 24px;
	line-height: 42px;
	color: rgba($body-color, 0.84);
}

.left-absolute-positioned-img {
	position: absolute;
    top: -50px;
    left: -80px;
    width: 37%;
    z-index: 1;

    img {
    	height: 100%;
		width: 100%;
		object-fit: contain;
		object-position: center;
    }

    @include media-breakpoint-only(xl) {
    	left: -150px;
    }

    @include media-breakpoint-down(xl) {
	    top: -20px;
	    left: -80px;
	    width: 50%;
	}

	@include media-breakpoint-down(md) {
	    left: -30px;
    	width: 70%;
    }
}

@include media-breakpoint-down(lg) {
	.container,
	.container-fluid {
		padding-left: 16px;
		padding-right: 16px;
	}

	.section-title {
		&:before {
			position: relative;
			margin-bottom: 16px;
			top: 0;
			left: 7px;
		}
	}
}

@include media-breakpoint-down(md) {
	.section-title {
		font-size: 32px;
		letter-spacing: -0.32px;
		line-height: 42px;
	}

	.section-content {
		font-size: 18px;
		line-height: 30px;
	}
}

@include media-breakpoint-down(sm) {
	.section-title {
		span {
			display: inline;
		}
	}
}