.section-title {
  position: relative;
  font-size: 52px;
  letter-spacing: -0.52px;
  line-height: 72px;
  margin: 0;
}
.section-title:before {
  content: "";
  position: absolute;
  top: 22px;
  left: -100px;
  display: block;
  width: 23px;
  height: 30px;
  background-color: #005EB8;
  transform: skewX(-20deg);
}
.section-title--secondary:before {
  background-color: #AA4322;
}
.section-title span {
  color: #5a5a5a;
  display: block;
}
@media (max-width: 1919.98px) {
  .section-title:before {
    left: -90px;
  }
}
.section-title.no-before-content:before {
  display: none;
}

.section-content {
  font-size: 24px;
  line-height: 42px;
  color: rgba(26, 26, 26, 0.84);
}

.left-absolute-positioned-img {
  position: absolute;
  top: -50px;
  left: -80px;
  width: 37%;
  z-index: 1;
}
