.news-detail {
	.news-date {
		margin: 20px 0 60px 0;
		font-size: 20px;
		font-weight: 500;
		line-height: 48px;
		letter-spacing: -0.2px;
		color: rgba($body-color, 0.80);
	}

	.news-poster {
		.news-poster-img {
			height: 726px;

			img {
				width: 100%;
				height: 100%;
				object-fit: cover;
				object-position: center;
			}
		}

		.poster-title {
			font-size: 14px;
			line-height: 16px;
			margin-top: 15px;
			position: relative;
			padding-left: 20px;

			&:before {
				content: '';
				position: absolute;
				left: 0;
				top: 3px;
				width: 20px;
			    height: 15px;
			    background-image: url('./../img/icons/caret-arrow-up.png');
				background-size: 10px;
			    background-repeat: no-repeat;
			}
		}
	}

	.social-icons {
		position: absolute;
		left: 8px;
    	top: 28%;

    	li {
    		text-align: center;

	    	a {
	    		display: inline-block;
	    		margin: 0 auto;

				img {
					width: 40px;
				}

				&:hover {
					filter: grayscale(100%);
				}
			}

			&:not(:last-child) {
				padding-bottom: 8px;
			}
		}

		p {
			color: rgba($body-color, 0.80);
			margin-bottom: 20px;
			font-family: $font-family-secondary;
			font-weight: 500;
		}
	}

	.news-content {
		margin-top: 60px;
		font-size: 18px;
		line-height: 30px;
		color: rgba($body-color, 0.84);

		h1,
		h2,
		h3,
		h4,
		h5 {
			font-weight: 500;
			margin-bottom: 32px;
			color: $body-color;
		}

		p {
			margin-bottom: 35px;
		}

		ol,
		ul {
			padding-left: 20px;

			> li {
				padding: 5px 0;
			}

			ol,
			ul {
				margin: 5px 0;

				li {
					padding: 5px 0 5px 10px;
					font-size: 14px;
					line-height: 20px;
					color: rgba($body-color, 0.80);
				}
			}
		}

		.list-specifications {
			margin-top: 60px;
			padding-left: 0;
			list-style-type: none;
			color: rgba($body-color, 0.84);
		    font-size: 18px;
		    line-height: 30px;

		    >li {
		    	position: relative;
    			padding: 2px 0 2px 20px;

			    &:before {
			    	content: "";
				    position: absolute;
				    left: 0;
			    	top: 11px;
			    	color: $primary;
				    display: inline-block;
				    border-top: 4px solid transparent;
				    border-left: 6px solid;
				    border-bottom: 4px solid transparent;
			    }
			}
		}

		blockquote {
			font-size: 26px;
			font-family: $font-family-secondary;
			line-height: 30px;
			letter-spacing: -0.26px;
			color: $body-color;
			padding-bottom: 15px;
			border-bottom: 2px solid $primary;
			width: 60%;
		}
	}
}

@include media-breakpoint-down(xxl) {
	.news-detail {
		.social-icons {
	    	top: 0;
		}
	}
}

@include media-breakpoint-down(xl) {
	.news-detail {
		.social-icons {
			top: 50px;
		}
	}
}

@include media-breakpoint-only(lg) {
	.news-detail {
		.social-icons {
			left: -80px;
		}
	}
}

@include media-breakpoint-down(md) {
	.news-detail {
		h1 {
			font-size: 25px;
			line-height: 34px;
			letter-spacing: -0.25px;
		}

		h2 {
			font-size: 22px;
			line-height: 30px;
			letter-spacing: -0.22px;
		}

		h3 {
			font-size: 20px;
			line-height: 27px;
			letter-spacing: -0.20px;
		}

		h4 {
			font-size: 18px;
			line-height: 24px;
			letter-spacing: -0.18px;
		}

		h5 {
			font-size: 16px;
			line-height: 21px;
			letter-spacing: -0.16px;
		}

		.news-date {
			margin: 20px 0;
			font-size: 16px;
			line-height: 28px;
			letter-spacing: -0.16px;
		}

		.news-poster {
			.news-poster-img {
				height: 511px;
			}

			.poster-title {
				font-size: 12px;
			}
		}

		.news-content {
			h1,
			h2,
			h3,
			h4,
			h5 {
				margin-bottom: 20px;
			}

			blockquote {
				font-size: 20px;
				line-height: 27px;
				letter-spacing: -0.2px;
				width: 100%;
			}
		}

		.social-icons {
			position: inherit;
		    display: flex;
		    align-items: center;
		    margin-top: 20px;
		    padding: 10px 0;
		    border-top: 1px solid rgba(112, 112, 112, 0.31);
		    border-bottom: 1px solid rgba(112, 112, 112, 0.31);

		    p {
		    	font-size: 12px;
		    	margin-right: 20px;
		    	margin-bottom: 0;
		    }

		    ul {
		    	margin-bottom: 0;

		    	li { 
		    		display: inline;

		    		&:not(:last-child) {
		    			padding-right: 8px;
		    		}

		    		img {
		    			width: 27px;
		    		}
		    	}
		    }
		}
	}
}

@include media-breakpoint-down(sm) {
	.news-detail {
		.news-poster {
			.news-poster-img {
				height: 250px;
			}
		}
	}
}