$grid-breakpoints: (
  	xs: 0,
  	sm: 768px,
  	md: 1024px,
  	lg: 1280px, //992px,
  	xl: 1920px, //1200px,
  	xxl: 2560px //1400px
) !default;

$container-max-widths: (
  	sm: 704px,
  	md: 980px,
  	lg: 1080px,
  	xl: 1640px,
  	xxl: 1920px
) !default;

$spacer: 1rem;
$spacers:(
	0: 0,
	1: $spacer * .25,
	2: $spacer * .5,
	3: $spacer,
	4: $spacer * 1.5,
	5: $spacer * 3,
	20: $spacer * 1.25,
	40: $spacer * 2.5,
	60: $spacer * 3.75,
	80: $spacer * 5,
	120: $spacer * 7.5,
	160: $spacer * 10,
	200: $spacer * 12.5,
);