.contact {
	.contact-details-wrapper {
		padding: 80px 30px;

		address {
			color: $white;
			font-size: 18px;
			line-height: 30px;

			a {
				color: $white;

				&:hover {
					opacity: 0.7;
				}
			}
		}

		.management-img {
			height: 280px;

			img {
				height: 100%;
				width: 100%;
				object-fit: cover;
				object-position: top;
			}
		}

		.contact-name {
			margin: 16px 0 0;
			font-size: 18px;
			line-height: 30px;
			color: $white;
		}

		.designation {
			font-weight: 500;
		}
	}
}

@include media-breakpoint-up(xl) {
	.contact {
		.contact-form-wrapper {
			.contact-form {
				width: 75%;
			}
		}
		.contact-details-wrapper {
			padding: 120px 160px;
		}
	}
}

@include media-breakpoint-down(xl) {
	.contact {
		.contact-form-wrapper {
			padding-right: 16px;
		}
		.contact-details-wrapper {
			.management-img {
				height: 212px;
				max-width: 185px;
			}
		}
	}
}

@include media-breakpoint-down(sm) {
	.contact {
		.container-fluid {
			> .row > * {
				padding-left: 16px !important;
				padding-right: 16px !important;
			}
		}

		.contact-details-wrapper {
			padding: 60px 0 !important;
		}
	}
}