footer {
	.company-info {
		padding: 120px 0 60px 0;

		p {
			font-size: 14px;
			line-height: 20px;
			color: rgba($body-color, 0.84);
		}

		h6,
		address {
			font-size: 20px;
			line-height: 30px;
		}

		h6 {
			font-weight: 500;
		}

		address,
		a {
			color: rgba($body-color, 0.84);
		}

		a {
			&:hover {
				color: $primary;
			}
		}
	}

	.footer-links {
		padding-top: 60px;
		padding-bottom: 185px;
		border-top: 1px solid rgba(112, 112, 112, 0.31);
		border-bottom: 1px solid rgba(112, 112, 112, 0.31);

		ul {
			li {
				padding-bottom: 5px;
			}
		}

		p,
		a,
		ul li a {
			color: $body-color;
			opacity: 0.84;
			font-size: 20px;
		}

		a {
			opacity: 1;

			&:hover,
			&.active {
				color: $primary;
			}
		}

		@at-root {
			h5,
			.footer-links .title-link {
				display: inline-block;
				margin-bottom: 20px;
				color: $body-color;
				font-size: 18px;
				letter-spacing: -0.18px;
				line-height: 30px;
				font-family: $font-family-secondary;
				font-weight: 600;
			}
		}
	}

	.copyright {
		font-size: 14px;
		line-height: 20px;
		opacity: 0.84;
		padding: 31px 0;

		ul {
			li {
				position: relative;

				a {
					color: $body-color;

					&:hover {
						color: $primary;
						opacity: 1;
					}
				}

				&:not(:last-child) {
					padding-right: 5px;

					&:after {
						position: absolute;
						content: "|";
						right: -5px;
					}
				}
			}
		}
	}
}

@include media-breakpoint-down(xxl) {
	footer {
		.footer-links {
			padding-bottom: 150px;
		}
	}
}

@include media-breakpoint-down(xl) {
	.divider-border-top {
		&:before {
			content: '';
			position: absolute;
			left: 0;
			right: 0;
			border-top: 1px solid rgba(112, 112, 112, 0.31);
			margin-top: -55px;
		}
	}
}

@include media-breakpoint-down(lg) {
	footer {
		.footer-links {
			padding: 60px 0 50px 0;
		}

		.copyright {
			font-size: 10px;
			line-height: 14px;
			padding: 18px 0;
		}
	}
}

@include media-breakpoint-down(sm) {
	footer {
		.company-info {
			padding: 80px 0 60px 0;
		}

		.footer-links {
			border-bottom: 0;
		}

		.copyright {
			a {
				color: rgba($body-color, 0.84);
			}

			.bg-light-gray {
				background-color: #EDEDED;
			}
		}
	}
}