.btn {
    border: 0;
    outline: 0;
    margin: 0 8px;
    padding: 12px 35px;
    font-size: 18px;
    font-family: $font-family-secondary;
    letter-spacing: -0.18px;
    line-height: 27px;
    transition: 0.3s ease all;
    transform: skew(-20deg);

    & > span { 
        display: inline-block; 
        transform: skew(20deg); 
    }

    &:before {
        content:'';
        position:absolute;
        top:0;
        left:50%;
        right:50%;
        bottom:0;
        opacity:0;
        transition:0.3s all ease;
    }

    &:hover {
        &:before {
            left:0;
            right:0;
            opacity:1;
            transition:0.3s all ease;
        }
    }

    &:focus {
        outline: none;
        box-shadow: none;
    }

    &.btn-primary {
        background: transparent linear-gradient(289deg, $primary 0%, #0077EA 100%) 0% 0% no-repeat;

        &.hover-orange {
            &:hover {
                background: transparent linear-gradient(289deg, $secondary 0%, #E05C31 100%) 0% 0% no-repeat;

                &:before {
                    background-color: $secondary;
                }
            }
        }

        &:hover {
            background: transparent linear-gradient(289deg, $primary 0%, #0077EA 100%) 0% 0% no-repeat;

            &:before {
                background-color: rgba($white, 0.2);
            }
        }

        &:before {
            background-color: $primary;
        }
    }

    &.btn-secondary {
        background: transparent linear-gradient(289deg, $secondary 0%, #E05C31 100%) 0% 0% no-repeat;

        &.hover-blue {
            &:hover {
                background: transparent linear-gradient(289deg, $primary 0%, #0077EA 100%) 0% 0% no-repeat;

                &:before {
                    background-color: $primary;
                }
            }
        }

        &:hover {
            background: transparent linear-gradient(289deg, $secondary 0%, #E05C31 100%) 0% 0% no-repeat;

            &:before {
                background-color: rgba($white, 0.2);
            }
        }

        &:before {
            background-color:$secondary;
        }
    }

    &.btn-white {
        border: 1px solid $white;
        background-color: $white;
        color: $body-color;

        &:hover {
            background-color: transparent;
            color: $white;
        }
    }

    &.btn-outline-white {
        border: 1px solid $white;
        color: $white;

        &:hover {
            background-color: $white;
            color: $body-color;
        }
    }

    &.btn-black {
        border: 1px solid $body-color;
        background-color: $body-color;
        color: $white;

        &:hover {
            background-color: transparent;
            color: $body-color;
        }
    }

    &.btn-outline-black {
        border: 1px solid rgba($body-color, 0.36);
        color: $body-color;
        font-weight: 500;

        &:hover {
            background-color: $body-color;
            color: $white;
        }
    }
}

@include media-breakpoint-down(lg) {
    .btn {
        font-size: 16px;
    }
}