.page-not-found {
	color: $white;

	h1 {
		font-size: 240px;
		line-height: 308px;
	}

	h1,
	h2 {
		font-style: italic;
	}

	p {
		font-size: 18px;
		line-height: 30px;
		margin: 32px 0;
		color: rgba($white, 0.84);
		max-width: 75%;
	}

	@include media-breakpoint-down(lg) {
		p {
			max-width: 100%;
		}
	}

	@include media-breakpoint-only(sm) {
		h1 {
			font-size: 100px;
			line-height: 120px;
		}

		h2 {
			font-size: 36px;
			line-height: 50px;
		}
	}

	@include media-breakpoint-down(sm) {
		h1 {
			font-size: 70px;
			line-height: 90px;
		}

		h2 {
			font-size: 24px;
			line-height: 32px;
		}

		p {
			font-size: 16px;
			line-height: 24px;
		}
	}
}