.accordion {
	.accordion-item {
		margin-bottom: 16px;

		.accordion-header {
			.accordion-button {
				color: $body-color;
				font-size: 26px;
				line-height: 30px;
				padding: 25px 30px;
				position: relative;
				z-index: 1;

				&:focus {
					box-shadow: none;
				}

				&:after {
					background-image: url('./../img/icons/plus.png');
				}

				&:not(.collapsed) {
					background-color: #ededed;

					&:after {
						background-image: url('./../img/icons/minus.png');
						transform: none;
					}
				}
			}
		}

		.accordion-body {
			padding: 0 30px 25px;
			font-size: 18px;
			line-height: 30px;
			background-color: #ededed;
			color: rgba($body-color, 0.84);

			p {
				margin-bottom: 25px;
			}

			.specifications-list {
				display: block;
				font-size: 18px;
				line-height: 30px;
				color: rgba($body-color, 0.84);

				>div {
					&:before {
						top: 13px;
					}
				}
			}

			.link-jd {
				margin-top: 40px;
				text-decoration: underline;
				display: inline-block;
				color: #0089ff;

				&:hover {
					color: $primary;
				}
			}
		}
	}
}

@include media-breakpoint-down(md) {
	.accordion {
		.accordion-item {
			.accordion-header {
				.accordion-button {
					font-size: 20px;
					line-height: 27px;
					padding: 20px;

					&:after {
						width: 1rem;
    					height: 1rem;
    					background-size: 1rem;
					}
				}
			}

			.accordion-body {
				padding: 0 20px 20px;
			}
		}
	}
}