header {
    position: relative;
    z-index: 2;
    border-bottom: 1px solid rgba(228, 228, 228, 0.2);

    .preheader {
        background-color: $black;
        height: 35px;

        a {
            &:hover {
                opacity: 0.5;
            }
        }

        p {
            line-height: 20px;
        }
    }

    .navbar {
        padding: 0;
        // padding: 30px 0;

        &.navbar-light {
            font-family: $font-family-secondary;

            .navbar-nav {
                .nav-item {
                    padding: 0 $nav-link-padding-x * 1.5;
                    // line-height: 30px;

                    .nav-link {
                        position: relative;
                        color: $body-color;
                        padding: 0;
                        font-size: 18px;
                        line-height: 127px;

                        &:before {
                            content: '';
                            position: absolute;
                            bottom: 0;
                            left: 50%;
                            transform: translate(-50%, 0);
                            width: 0;
                            transition: width 250ms ease-in-out;
                            // width: 100%;
                            border-bottom: solid 6px $secondary;  
                            // transform: scaleX(0);  
                            // transition: transform 250ms ease-in-out;
                        }

                        &:hover,
                        &.active,
                        &.show {
                            &:before {
                                width: 100%;
                                // transform: scaleX(1);
                            }
                        }
                    }

                    &.dropdown {
                        .dropdown-toggle::after {
                            margin-left: 15px;
                            vertical-align: middle;
                        }

                        .dropdown-menu {
                            box-shadow: rgba($black, 0.16) 0px 1px 3px;
                            border: 0;
                            transition: all 0.3s linear;
                            padding: 0;
                            border-radius: 0;
                            min-width: auto;
                            width: 100%;
                            left: 0;
                            margin-top: 0;

                            .dropdown-item {
                                padding: 12px 20px;
                            }
                        }

                        @include media-breakpoint-up(lg) {
                            &:hover {
                                .dropdown-menu {
                                    display: block;
                                }

                                .nav-link {
                                    &:before {
                                        width: 100%;
                                    }
                                }
                            }
                        }
                    }

                    &.language-dropdown {
                        .nav-link {
                            font-size: 16px;
                        }
                    }
                }
            }

            .btn {
                padding: 7px 33px;
            }

            .navbar-toggler {
                .navbar-toggler-icon {
                    background-image: url('./../img/icons/menu.png');
                }

                &[aria-expanded="true"] {
                    .navbar-toggler-icon {
                        background-image: url('./../img/icons/close.png');
                    }
                }
            }   
        }

        &.sticky {
            position: fixed;
            z-index: 1;
            left: 0;
            right: 0;
            top: -97px;
            width: 100%;
            background-color: $white;
            transform: translateY(96px);
            transition: transform 1s, -webkit-transform 1s;
            box-shadow: 0 0 10px rgba($black, 0.1);

            .navbar-nav {
                .nav-item {
                    .nav-link {
                        line-height: 100px;
                    }
                }
            }
        }
    }

    .navbar-toggler {
        &:focus {
            outline: 0;
            box-shadow: none;
        }
    }
}

@include media-breakpoint-down(xl) {
    header {
        .preheader {
            p {
                font-size: 13px;
            }
        }

        .navbar {
            &.navbar-light {
                .navbar-brand {
                    img {
                        height: 42px;
                    }
                }

                .navbar-nav {
                    .nav-item {
                        padding: 0 $nav-link-padding-x;

                        .btn {
                            margin-right: 7px;
                        }
                    }
                }
            }
        }
    }
}

@include media-breakpoint-down(lg) {
    header {
        .navbar {
            &.navbar-light {
                padding: 30px 0;

                .navbar-collapse {
                    position: absolute;
                    left: 0;
                    top: 111px;
                    width: 100%;
                    z-index: 1;
                    background: $primary;
                }

                &.sticky {
                    padding: 15px 0;

                    .navbar-collapse {
                        top: 81px;
                    }
                }

                .navbar-nav {
                    &:first-child {
                        padding-top: 20px;
                    }

                    &:last-child {
                        padding-bottom: 20px;
                    }

                    .nav-item {
                        padding: 5px 16px 0;

                        .nav-link {
                            display: inline-block;
                            color: $white;
                            padding-bottom: 10px;
                            line-height: 35px;

                            &:hover,
                            &.active {
                                color: $white;
                                opacity: 0.8;
                            }

                            &:before {
                                border-bottom-color: $white;
                            }
                        }

                        .dropdown-item {
                            &.active,
                            &:active {
                                background-color: $secondary;
                            }
                        }
                    }
                }
            }
        }
    }
}

@include media-breakpoint-only(lg) {
    header {
        .container,
        .container-lg {
            max-width: 1260px;
        }
    }
}