.thank-you {
	h1 {
		font-size: 68px;
		line-height: 86px;
		letter-spacing: -0.68px;
	}

	@include media-breakpoint-down(md) {
		h1 {
	        font-size: 36px;
	        line-height: 42px;
	        letter-spacing: -0.36px;
	    }
	    h2 {
			font-size: 22px;
			line-height: 30px;
			letter-spacing: -0.22px;
		}
	}
}