.products-category-tab-wrapper {
	position: -webkit-sticky; 
	position: sticky; 
	top: 99px; 
	z-index: 1;

	&:before {
		content: '';
		position: absolute;
		bottom: -2px;
		width: 100%;
		height: 4px;
		background-color: #d5d5d5;
	}
	
	ul {
		position: relative;
		margin-bottom: -2px;

		.list-inline-item {
			border-bottom: 4px solid transparent;

			&:not(:last-child) {
		    	margin-right: 60px;
		    }

		    a {
		    	display: inline-block;
				padding: 32px 0;
				font-size: 20px;
				letter-spacing: -0.2px;
				line-height: 48px;
				font-family: $font-family-secondary;
		    	color: $body-color;
		    }

	    	&.active {
	    		border-bottom-color: $secondary;
	    		text-shadow: 0 0 1px $black;
	    	}
		}

		// total width
		&::-webkit-scrollbar {
		    width: 16px;
		    height: 4px;
		}

		// scrollbar itself 
		&::-webkit-scrollbar-thumb {
		    background-color: #babac0;
		    border-radius: 16px;
		    border: 5px solid #ada5a5;
		}

		&::-webkit-scrollbar-thumb:hover {
		    border: 4px solid #9b9696;
		}

		// set button(top and bottom of the scrollbar) 
		&::-webkit-scrollbar-button {
			display: none;
		}
	}
}

.product-tab-content {
	position: relative;
	overflow: hidden;

	p {
		font-size: 32px;
		line-height: 48px;
		letter-spacing: -0.32px;
		color: #5a5a5a;
	}

	.product-tab-details {
		.product-tab-img {
			position: relative;
			margin: 0 auto;
			text-align: center;
			padding: 280px 0 120px;

			&:before {
				content: '';
				position: absolute;
	   			left: 50%;
	   			top: 0;
				width: 250px;
	    		height: 536px;
	    		background: transparent linear-gradient(180deg, $secondary 0%, #E05C31 100%) 0% 0% no-repeat padding-box;
	    		transform: skewX(20deg) translateX(-50%);
			}

			img {
				position: relative;
				max-width: 90%;
			}
		}

		.specifications-list {
			color: rgba($body-color, 0.84);
		}
	}

	.bg-skew {
		position: absolute;
		bottom: 0;
		left: -80px;
		width: 56.5%;
    	height: 358px;
    	transform: skewX(25deg);

    	&.bg-skew-gray {
    		background-color: $body-bg;
    	}

    	&.bg-skew-white {
    		background-color: $white;
    	}
	}
}

.product-banner {
	position: relative;
	height: 895px;
	display: flex;
	align-items: center;
	justify-content: center;

	.product-banner-img {
		position: relative;
	    width: 100%;
	    height: 100%;

	    img {
	    	width: 100%;
		    height: 100%;
		    object-fit: cover;
		    object-position: bottom;
	    }

		&:before {
			content: '';
			position: absolute;
			height: 100%;
			left: 0;
			right: 0;
			background-color: rgba($black, 0.5);
		}
	}

	.product-banner-content {
		position: absolute;

		h3 {
			font-size: 36px;
			line-height: 53px;
			letter-spacing: -0.36px;
			color: $white;
		}
	}
}

.product-grid {
	h4 {
		font-size: 22px;
		line-height: 32px;
  		letter-spacing: -0.22px;
  		text-transform: uppercase;
	}

	p {
		font-size: 20px;
		line-height: 30px;
		color: rgba($body-color, 0.8);
		margin: 0;;
	}

	.product-row {
		.product-col {
			>div {
				padding: 16px;
			}

			.product-image {
				margin: 50px 0;
				height: 350px;

				img {
					width: 100%;
					height: 100%;
					object-fit: contain;
				}
			}

			.product-details {
				position: relative;
				padding-left: 30px;

				&:before {
					content: '';
					position: absolute;
					left: 0;
					top: 6px;
					width: 20px;
				    height: 32px;
				    background-image: url('./../img/icons/caret-arrow-up-secondary.png');
					background-size: 15px;
				    background-repeat: no-repeat;

				    @include media-breakpoint-up(xxl) {
				    	top: 9px;
				    }

				    @include media-breakpoint-down(md) {
				    	top: 8px;
				    }

					@include media-breakpoint-down(sm) {
						top: 5px;
					}
				}
			}

			@include media-breakpoint-up(xxl) {
				border-top: 1px solid rgba($black, 0.18);
			}

			@include media-breakpoint-down(xxl) {
				&:before {
					content: '';
					position: absolute;
					height: 1px;
					width: 100%;
					left: 0;
					background-color: rgba($black, 0.18);
				}
			}

			@include media-breakpoint-up(md) {
				&:nth-child(odd) {
					border-right: 1px solid rgba($black, 0.18);
				}
				>div {
					padding: 25px;
				}
			}
		}
	}
}

@include media-breakpoint-only(xl) {
	.product-tab-content {
		.bg-skew {
			width: 58.5%;
		}
	}
	.product-banner {
		height: 672px;
	}
}

@include media-breakpoint-down(xl) {
	.products-category-tab-wrapper {
		top: 80px; 

		ul {
			display: flex;
			overflow-y: hidden;
			overflow-x: auto;

			.list-inline-item {
				flex: none;
    			display: flex;
				align-items: stretch;
			}
		}
	}

	.product-tab-content {
		.product-tab-details {
			.product-tab-img {
				&:before {
					width: 200px;
		    		height: 450px;
				}
			}
		}
	}

	.product-grid {
		.product-row {
			.product-col {
				.product-image {
					margin: 20px 0;
	    			height: 200px;
				}
			}
		}
	}
}

@include media-breakpoint-only(lg) {
	.product-tab-content {
		.bg-skew {
			height: 280px;
			width: 61%;
		}

		.product-tab-details {
			.product-tab-img {
				padding: 280px 0 80px;
			}
		}
	}
	.product-banner {
		height: 672px;
	}
}

@include media-breakpoint-down(lg) {
	.product-tab-content {
		p,
		.specifications-list {
			font-size: 18px;
			line-height: 30px;
		}

		h3 {
			font-size: 20px;
			line-height: 27px;
		}

		.specifications-list {
			margin-bottom: 40px;
			
			> div {
				&:before {
					top: 12px;
				}
			}
		}

		.bg-skew {
			height: 250px;
			width: 100%;
		}

		.product-tab-details {
			.product-tab-img {
				padding: 280px 0 0;
			}

			.specifications-list {
				grid-template-columns: 200px 200px;
			}
		}
	}
	.product-banner {
		height: 350px;

		.product-banner-content {

			h3 {
				font-size: 20px;
				line-height: 30px;
				letter-spacing: -0.2px;
			}
		}
	}
}

@include media-breakpoint-down(md) {
	.products-category-tab-wrapper {
		ul {
			.list-inline-item {
				&:not(:last-child) {
			    	margin-right: 40px;
			    }

			    a {
					padding: 25px 0;
					font-size: 14px;
					letter-spacing: -0.14px;
					line-height: 18px;
			    }
			}
		}
	}

	.product-tab-content {
		.bg-skew {
			height: 170px;
		}

		.product-tab-details {
			.product-tab-img {
				padding: 150px 0 0;

				&:before {
					width: 170px;
    				height: 315px;
				}
			}
		}
	}

	.product-banner {
		height: auto;
		flex-direction: column;
		background-color: $body-color;

		.product-banner-img {
			height: 247px;
			clip-path: polygon(0% 0, 100% 0, 100% 80%, 0% 100%);
		}

		.product-banner-content {
			position: relative;
			width: 100%;
			padding: 30px 0 80px;
			
			h3 {
				font-size: 20px;
				line-height: 30px;
				letter-spacing: -0.2px;
			}
		}
	}
}

@include media-breakpoint-down(sm) {
	.products-category-tab-wrapper {
		&:before {
			height: 2px;
			bottom: -1px;
		}

		ul {
			// padding-bottom: 5px;

			// .list-inline-item {
			// 	max-width: 140px;
			// }
		}
	}

	.product-tab-content {
		.bg-skew {
			transform: skewX(-20deg);
			left: auto;
			right: -40px;
			height: 130px;
		}

		.product-tab-details {
			.product-tab-img {
				&:before {
					width: 120px;
    				height: 220px;
				}
			}

			.specifications-list {
				grid-template-columns: auto auto;
			}
		}
	}

	.product-grid {
		background-color: $body-bg;
		
		h4 {
			font-size: 16px;
			line-height: 24px;
	  		letter-spacing: -0.16px;
		}

		p {
			font-size: 16px;
			line-height: 22px;
		}
	}
}