.form-floating {
	> .form-control:focus, 
	> .form-control:not(:placeholder-shown), 
	> .form-select {
		~ label {
			font-size: 14px;
			line-height: 20px;
			opacity: 0.84;
			transform: scale(0.85) translateY(-0.8rem) translateX(0.15rem);
		}
	}

	.form-control,
	.form-select {
		height: auto;
		font-size: 18px;
		line-height: 30px;
		border-bottom-width: 2px;

		&:focus {
			outline: none;
			border-color: $body-bg;
			box-shadow: none;
			border-bottom-color: $primary;

			&::-webkit-input-placeholder {
			  	opacity: 1;
			  	color: rgba($black, 0.51);
			}
		}

		&::-webkit-input-placeholder {
		  	opacity: 0;
		  	transition: inherit;
		}
	}

	.form-control,
	.form-select,
	.form-control:focus,
	.form-select:focus,
	.form-control:not(:placeholder-shown) {
		padding: 25px 10px 10px;
	}

	> label {
    	padding: 1.3rem 0.75rem;
    }

    .form-select {
    	-webkit-appearance: none;
    	// background-image: url('../img/icons/arrow-selectbox.png');
    }
}