.information {
	position: relative;
	overflow: hidden;

	.bg-skew {
		position: absolute;
		top: 0;
		width: 40%;
    	height: 100%;
    	transform: skewX(-15deg);

		&-primary {
    		background: transparent linear-gradient(180deg, $primary 0%, #0077EA 100%) 0% 0% no-repeat padding-box;
    	}

    	&-secondary {
    		background: transparent linear-gradient(180deg, $secondary 0%, #E05C31 100%) 0% 0% no-repeat padding-box;
    	}

    	&-body {
    		background: $body-color;
    	}

    	&-left {
    		left: 0;
    	}

    	&-right {
    		right: 0;
    	}

    	&.is-small {
    		width: 30%;
    	}
	}

	.information-video-wrapper,
	.information-img-wrapper {
		position: relative;
		z-index: 1;
		width: 100%;
	}

	.information-video-wrapper {
		video {
			width: 100%;
			min-height: 302px;
			object-fit: cover;
		}
	}

	.information-img-wrapper {
	    width: 620px;
		height: 430px;
	    margin: 0 auto;
	    overflow: hidden;
	    transform: skewX(-15deg);

		img {
			transform: skewX(15deg);
			position: relative;
			top: -45px;
			left: -50px;
		}
	}

	&.has-skewed-img {
		@include media-breakpoint-only(xxl) {
			.bg-skew {
				&-left {
		    		left: 115px;
		    	}

		    	&-right {
		    		right: 110px;
		    	}
			}
		}

		@include media-breakpoint-down(xl) {
			.bg-skew {
			    top: auto;
				bottom: 0;
			    // transform: skew(0);
			    height: 700px;
			    margin: 0;
			    width: 75%;

			    &-left {
			    	left: -100px;
		    	}

		    	&-right {
		    		right: -100px;
		    	}
			}

			.information-img-wrapper {
				margin: 130px 0;
			}
		}

		p {
			font-size: 24px;
			color: $body-color;
			line-height: 42px;
		}
	}

	h2 {
		margin-bottom: 25px;
	}

	h4 {
		font-weight: 600;
	}

	p {
		font-size: 22px;
		margin-bottom: 30px;
		line-height: 35px;
		color: rgba($body-color, 0.84);
	}

	@at-root {
		.specifications-list {
			display: grid;
			grid-template-columns: 300px 300px;
			font-size: 24px;
			line-height: 42px;

			> div {
				position: relative;
				padding: 2px 0 2px 20px;
				// padding-left: 20px;

				&:before {
					content: '';
					position: absolute;
					left: 0;
	    			top: 18px;
					color: $body-color;
					display: inline-block;
					border-top: 4px solid transparent;
				    border-left: 6px solid;
				    border-bottom: 4px solid transparent;
				}
			}
		}
	}
}

@include media-breakpoint-only(lg) {
	.information {
		.bg-skew {
			height: calc(100% - 50px);
			transform: skewX(-15deg);
		    margin: 25px 0;
		    min-height: 530px;
		}
	}
}

@include media-breakpoint-down(md) {
	.information {
		h4 {
			font-size: 20px;
			line-height: 27px;
		}

		h2 {
			font-size: 32px;
			line-height: 34px;
			letter-spacing: -0.32px;
		}

		p,
		&.has-skewed-img p {
			font-size: 18px;
			line-height: 30px;
		}

		.specifications-list {
			font-size: 18px;
			line-height: 30px;
    		grid-template-columns: auto auto;

    		> div {
			    padding-left: 10px;

			    &:before {
				    top: 12px;
				}
			}
		}

		.bg-skew {
			display: none;
		}

		&.has-skewed-img {
			.information-img-wrapper {
				margin: 0;
				transform: skew(0);

				img {
					transform: skew(0);
					width: 100%;
					height: 100%;
					left: 0;
					top: 0;
				}
			}
		}
	}
}

@include media-breakpoint-down(sm) {
	.information {
		.information-video-wrapper {
			video {
				min-height: 192px;
			}
		}
	}
}