h1,h2,h3,h4,h5,h6,
.h1,.h2,.h3,.h4,.h5,.h6 {
	font-weight: $font-weight-normal;
	font-family: $font-family-secondary;
}

h1,
.h1 {
	font-size: 52px;
	line-height: 64px;
	letter-spacing: -0.52px;
}

h2,
.h2 {
	font-size: 42px;
	line-height: 52px;
	letter-spacing: -0.42px;
}

h3,
.h3 {
	font-size: 32px;
	line-height: 40px;
	letter-spacing: -0.32px;
}

h4,
.h4 {
	font-size: 26px;
	line-height: 33px;
	letter-spacing: -0.26px;
}

h5,
.h5 {
	font-size: 18px;
	line-height: 23px;
	letter-spacing: -0.18px;
}
