.quality-control-slider-wrapper {
    background: transparent linear-gradient(180deg, #005EB8 0%, #0077EA 100%) 0% 0% no-repeat padding-box;
    position: absolute !important;
    left: 73px;
    width: 91%;
    padding: 200px 30px 50px;
    overflow: hidden;
    transform: skew(-15deg);

    .row {
    	transform: skew(15deg);
    }

    h3 {
    	color: $white;    	
    }

    .logo {
    	height: 45px;
    }

    .product-img-wrapper {
    	img {
    		height: 100%;
    		width: 100%;
    		object-fit: contain;
    	}
    }
}

.quality-control-slider--nav {
	position: absolute;
    top: 0;
    right: 2%;
    display: flex !important;
    align-items: center;
    background: #2c2c2c;
    transform: skew(-15deg);
    height: 132px;
    width: 60%;
    padding: 10px 80px;
    z-index: 1;

    .slick-arrow {
	    position: relative;
    	display: flex;
    	justify-content: center;
	    background: $white;
	    width: 40px;
	    height: 40px;
	    border-radius: 50%;
   		z-index: 1;	
   		cursor: pointer;
   		transform: skew(15deg);
	    box-shadow: 0px 12px 32px rgba($black, 0.17);

	    img {
	    	width: 10px;
	    }

	    + .slick-arrow {
	    	margin-left: 10px;
	    }

	    &.slick-disabled {
	    	opacity: 0.4;
	    }
	}
}

.slider-counter {
	position: absolute;
    top: 8px;
    right: 20px;
    font-family: $font-family-secondary;
    font-size: 16px;
    letter-spacing: -0.16px;
    font-weight: 500;
    color: $white;
    pointer-events: none;

    @include media-breakpoint-up(lg) {
    	font-size: 20px;
    	line-height: 32px;
    	top: 50%;
    	transform: translateY(-50%) skew(15deg);
    }
}

.quality-control-slider-wrapper,
.manufacturing-execution-system {
	h3 {
		font-size: 36px;
    	letter-spacing: -0.36px;
    	line-height: 52px;
	}
}

.manufacturing-execution-system {
	padding-top: 450px;
	padding-bottom: 120px;
	margin-top: 300px;
	background-color: #2C2C2C;

	.specifications-list {
		>div {
			font-size: 26px;
			line-height: 46px;
			color: rgba($white, 0.8);

			&:before {
				color: rgba($white, 0.8);
			}
		}
	}
}

.certifications-img-wrapper {
	background-color: $white;
	height: 194px;
	padding: 35px;

	img {
		height: 100%;
		width: 100%;
		object-fit: contain;
		object-position: center;
	}
}

@include media-breakpoint-up(xxl) {
	.quality-control-slider-wrapper {
	    .product-img-wrapper {
	    	height: 368px;
	    }
	}
}

@include media-breakpoint-only(xl) {
	.quality-control-slider-wrapper {
	    .product-img-wrapper {
	    	height: 310px;
	    }
	}

	.quality-control-slider--nav {
	    right: 1.1%;
	}
}

@include media-breakpoint-only(lg) {
	.quality-control-slider-wrapper {
		left: 92px;
    	width: 83.4%;

    	.product-img-wrapper {
	    	height: 275px;
	    }
	}

	.quality-control-slider--nav {
	    right: 2.3%;
		width: 83.4%;
	}
}

@include media-breakpoint-down(lg) {
	.quality-control-slider-wrapper {
		position: initial !important;
	    left: 0;
	    width: 100%;
	    padding: 90px 30px 60px;
	    transform: skew(0);

	    .row {
		    transform: skew(0);
		}

		h3 {
			font-size: 20px;
			line-height: 30px;
			letter-spacing: -0.2px;
		}

		.product-img-wrapper {
	    	height: 250px;
	    }
	}

	.quality-control-slider--nav {
		width: 100%;
		left: 0;
		right: 0;
		padding: 5px 30px;
		height: 40px;
		transform: skew(0);

		.slick-arrow {
			transform: skew(0);
			height: 25px;
			width: 25px;

			img {
				width: 5px;
			}
		}
	}

	.manufacturing-execution-system {
		margin-top: 0;
		padding-top: 120px;
	}
}

@include media-breakpoint-down(md) {
	.manufacturing-execution-system {
		padding: 80px 0;

		h3 {
			font-size: 22px;
			line-height: 32px;
			letter-spacing: -0.22px;
		}
	}

	.manufacturing-execution-system,
	.certifications {
		.specifications-list {
			>div {
				font-size: 16px;
				line-height: 28px;

				&:before {
					top: 12px;
				}
			}
		}
	}

	.certifications-img-wrapper {
		height: 164px;
	}

	.quality-control-slider-wrapper {
		padding: 100px 16px 60px;

		.product-img-wrapper {
			height: 180px;
			margin-bottom: 30px;
		}
	}
}