.about {
	p,
	h5,
	address {
		font-size: 24px;
		line-height: 42px;
	}

	h5 {
		font-weight: 500;
	}

	p {
		color: rgba($black, 0.84);
		margin-bottom: 150px;
	}

	address {
		a {
			color: $body-color;

			&:hover {
				opacity: 0.6;
			}
		}
	}

	.logo {
		height: 25px;
	}

	.location-wrapper {
		.location-img {
			height: 450px;

			img {
				width: 100%;
				height: 100%;
				object-fit: cover;
				object-position: center;
			}
		}
	}
}

.history {
	background-color: #272727;

	.history-slider {
		p {
			font-size: 24px;
			line-height: 42px;
			color: rgba($white, 0.84);
		}

		.slick-dots {
		    position: absolute;
		    left: 100px;
		    top: 0;
		    right: 0;
		    display: flex;
    		justify-content: space-around;
		    list-style: none;
		    padding: 0;
		    margin: 0;

		    &:before {
		    	content: '';
		    	position: absolute;
		    	bottom: 9px;
		    	width: 100%;
		    	height: 1px;
		    	background-color: rgba($white, 0.6);
		    }

		    li {
			    position: relative;
			    display: inline-block;
			    height: 80px;
			    font-family: $font-family-secondary;

			    button {
				    display: flex;
				    height: 80px;
				    font-size: 32px;
				    line-height: 40px;
				    letter-spacing: -0.32px;
				    color: rgba($white, 0.6);
				    border: 0;
				    padding: 0;
				    background-color: transparent;
				    transition: transform 0.3s ease-out;

				    &:focus-visible,
				    &:focus {
					    outline: 0;
					}
				}

				&.slick-active {
					button {
						color: $white;
						transform: scale(1.5);
					}
				}

				&:before {
					content: '';
					position: absolute;
					bottom: 0;
					left: 50%;
					transform: translateX(-50%);
					width: 19px;
					height: 19px;
					border-radius: 50%;
					border: 3px solid rgba($white, 0.6);
					background-color: $body-color;
					cursor: pointer;
				}

				&.slick-active {
					&:before {
						border-color: $white;
					}
				}
			}
		}

		.slick-slide {
			padding: 0 2px;
		}
	}
}

.history-slider--nav {
	position: absolute;
    top: 50px;
    left: 0;
    display: flex !important;
    align-items: center;
    width: 100px;
    background-color: #272727;
    z-index: 1;

    .slick-arrow {
	    position: relative;
    	display: flex;
    	justify-content: center;
	    background: $white;
	    width: 40px;
	    height: 40px;
	    border-radius: 50%;
   		z-index: 1;	
   		cursor: pointer;
	    box-shadow: 0px 12px 32px rgba($black, 0.17);

	    img {
	    	width: 10px;
	    }

	    + .slick-arrow {
	    	margin-left: 10px;
	    }

	    &.slick-disabled {
	    	opacity: 0.4;
	    }
	}
}

@include media-breakpoint-only(lg) {
	.about {
		p {
			margin-bottom: 107px;
		}

		.location-wrapper {
			.location-img,
			iframe {
				height: 295px;
			}
		}
	}
}

@include media-breakpoint-down(lg) {
	.about {
		h2 {
			font-size: 32px;
	   		line-height: 42px;
	   		letter-spacing: -0.32px;
		}

		p,
		h5,
		address {
			font-size: 18px;
	   		line-height: 30px;
		}

		p {
			margin-bottom: 0;
		}

		.location-wrapper {
			.location-img,
			iframe {
				height: 295px;
			}
		}
	}
}

@include media-breakpoint-down(md) {
	.history {
		.history-slider {
			position: relative;

			&:before {
				content: '';
		    	position: absolute;
		    	top: 0;
		    	left: 0;
		    	width: 1px;
		    	height: 100%;
		    	background-color: $white;
			}

			h2 {
				position: relative;
				font-size: 20px;
			    line-height: 27px;
			    letter-spacing: -0.2px;

			    &:before {
					content: '';
					position: absolute;
					left: -25px;
					top: 8px;
					width: 10px;
					height: 10px;
					border-radius: 50%;
					border: 3px solid $white;
					background-color: $body-color;
				}
			}

			p {
				font-size: 18px;
			    line-height: 30px;
			}

			>div {
				padding-left: 20px;

				&:not(:last-child) {
					margin-bottom: 80px;
				}
			}

			.history-banner-img {
				height: 300px;

				img {
					width: 100%;
					height: 100%;
					object-fit: cover;
					object-position: center;
				}
			}
		}
	}

	.about {
		.section-title {
			font-size: 36px;
			letter-spacing: -0.36px;
			line-height: 42px;
		}
	}
}

@include media-breakpoint-only(sm) {
	.about {
		.location-wrapper {
			.location-img,
			iframe {
				height: 350px;
			}
		}
	}
}

@include media-breakpoint-down(sm) {
	.about {
		.location-wrapper {
			.location-img,
			iframe {
				height: 230px;
			}
		}
	}
}