.banner {
	position: relative;
	height: 913px;
	display: flex;
	align-items: flex-end;

	>div {
		position: relative;
		z-index: 1;
	}

    &.services-banner {
    	background: url('./../img/banner/services.svg') no-repeat;
    	background-size: cover;
    }

    @include media-breakpoint-down(md) {
    	align-items: center;
    }
}

.banner-video {
	position: relative;

	&:before {
		content: '';
		position: absolute;
		top: 0;
		bottom: 0;
		left: 0;
		right: 0;
		background-color: rgba($black, 0.6);
	}

	.banner-video-wrapper {
		height: 913px;

		video {
			object-fit: cover;
		    width: 100%;
		    height: 100%;
		}
	}

	.banner-video-content {
		position: absolute;
		bottom: 0;
    	width: 100%;
	}
}

.banner,
.banner-video {
	position: relative;

	&:before {
		content: '';
		position: absolute;
		top: 0;
		bottom: 0;
		left: 0;
		right: 0;
		background-color: rgba($black, 0.6);
	}

	h1,
	h4 {
		color: $white;
	}

	h4 {
        font-size: 24px;
        line-height: 32px;
        letter-spacing: -0.24px;
    }

    h1 {
        margin: 20px 0 40px 0;
    }

    .badge-img-wrapper {
    	display: flex;
    	align-items: center;
    	flex-wrap: wrap;

    	>div {
    		margin: 10px 15px 10px 0;

    		img {
    			width: 56px;
    		}

    		&:first-child {
    			margin-left: 0;
    		}

    		&:last-child {
    			margin-right: 0;
    		}
    	}
    }
}

@include media-breakpoint-only(xl) {
	.banner,
	.banner-video .banner-video-wrapper {
		height: 651px;
	}
}

@include media-breakpoint-down(xl) {
	.banner,
	.banner-video .banner-video-wrapper {
		height: 448px;
	}
}

@include media-breakpoint-down(md) {
	.banner,
	.banner-video {
		h4 {
	        font-size: 16px;
	        line-height: 20px;
	    }

	    h1 {
	        font-size: 36px;
	        line-height: 42px;
	        letter-spacing: -0.36px;
	        margin-bottom: 0;
	    }

	    .badge-img-wrapper {
	    	>div {
	    		img {
	    			width: 30px;
	    		}
	    	}
	    }
	}

	.banner-video {
		.banner-video-content {
			top: 50%;
			bottom: auto;
			transform: translateY(-50%);
		}
	}
}