.cta {
	position: relative;
	padding: 250px 0;
	background: transparent linear-gradient(82deg, #005EB8 0%, #0077EA 100%) 0% 0% no-repeat padding-box;

	.cta-banner-wrapper {
	    position: absolute;
	    top: 0;
	    bottom: 0;
	    right: 0;
	    left: calc(50% + 16px);
	    clip-path: polygon(20% 0, 100% 0, 100% 100%, 0% 100%);

	    img {
	    	width: 100%;
		    height: 100%;
		    object-fit: cover;
		    object-position: left;
	    }
	}

	h3 {
		margin-bottom: 40px;
	}

	.btn {
		font-size: 26px;
		line-height: 47px;
		font-weight: 500;
		letter-spacing: -0.26px;
	}	
}

@include media-breakpoint-only(xl) {
	.cta {
		padding: 120px 0;

		.btn {
			font-size: 20px;
			line-height: 29px;
		}
	}
}

@include media-breakpoint-down(xl) {
	.cta {
		padding: 127px 0;

		.cta-banner-wrapper {
			left: calc(55% + 16px);
		}

		.btn {
			font-size: 20px;
			line-height: 29px;
		}
	}
}

@include media-breakpoint-only(lg) {
	.cta {
		.cta-banner-wrapper {
			left: calc(59% + 16px);
			clip-path: polygon(40% 0, 100% 0, 100% 100%, 0% 100%);
		}
	}
}

@include media-breakpoint-down(lg) {
	.cta {
		h3 {
			font-size: 20px;
			line-height: 30px;
			letter-spacing: -0.2px;
		}
	}
}

@include media-breakpoint-between(sm, md) {
	.cta {
		.cta-banner-wrapper {
		    clip-path: none;
		    left: calc(63% + 16px);

			img {
				object-position: revert;
			}
		}
	}
}

@include media-breakpoint-down(sm) {
	.cta {
		padding-top: 0;
		padding-bottom: 50px;

		.cta-banner-wrapper {
		    position: relative;
		    left: 0;
		    clip-path: polygon(0 0, 100% 0, 100% 100%, 0% 80%);
		}

		h3 {
			margin: 30px 0;
		}

		.btn {
			font-size: 16px;
		}
	}
}