.hero {
    .hero-video-wrapper {
        position: relative;

        video {
            object-fit: cover;
            width: 100%;
            height: 100%;
        }

        &:before {
            content: '';
            position: absolute;
            left: 0;
            right: 0;
            top: 0;
            bottom: 0;
            background-color: rgba($black, 0.3);
        }
    }

    .card-product-hero {
        padding: 64px 50px;
        position: relative;
        z-index: 1;
        transition: background-color 0.3s ease-in;

        .card-product-hero-img {
            position: relative;
            padding: 80px 0;

            img {
                max-height: 250px;  
                width: 100%;
                height: 100%;
                object-fit: contain;
                object-position: left;
                transition: all 0.3s ease-in;
            }
        }

        &:before {
            content: "";
            position: absolute;
            top: 20px;
            left: 120px;
            background-color: #F2F2F2;
            height: 300px;
            width: 120px;
            transform: skew(-25deg);
            transition: background-color 0.3s ease-in;
        }

        .card-product-hero-content {
            position: relative;

            h4 {
                font-family: $font-family-secondary;
                font-size: 23px;
                line-height: 29px;
                letter-spacing: -0.23px;
                margin: 0 0 20px 0;
                text-align: left;
                text-transform: initial;
            }

            a {
                font-style: italic;
                position: relative;
                color: $body-color;
                z-index: 1;
            }
        }

        &:hover {
            background-color: $secondary;

            &:before {
                background-color: rgba($black, 0.05);
            }

            .card-product-hero-img {
                img {
                    transform: scale(1.1);
                }
            }

            .card-product-hero-content {
                h4,
                a {
                    color: $white;
                }

                .link-learn-more {
                    svg {
                        transition: none;
                    }
                }
            }
        }
    }
}

.hero-content {
    position: absolute;
    bottom: 300px;
    color: $white;
    width: 100%;

    h4 {
        font-size: 24px;
        line-height: 32px;
        letter-spacing: -0.24px;
    }

    h1 {
        margin: 20px 0 40px 0;
    }
}

.hero-content-btns {
    background-color: $body-color;
    padding-top: 50px;
}

.hero-slider {
    background-color: $body-color;
    padding: 50px 0;

    .container {
        max-width: calc(1920px - 31%);
    }

    .hero-slider-wrapper {
        h5 {
            font-size: 16px;
            line-height: 22px;
            letter-spacing: 0.64px;
            text-transform: uppercase;
        }

        p {
            color: rgba($white, 0.84);
            font-size: 14px;
            line-height: 20px;
            margin: 0;
        }

        .slick-slide {
            margin: 0 30px;
        }

        > div {
            padding-right: 30px;
        }
    }
}

@include media-breakpoint-up(xxl) {
    .hero {
        .hero-video-wrapper {
            height: 1047px;
        }
    }
}

@include media-breakpoint-down(xl) {
    .hero {
        .hero-video-wrapper {
            height: 437px;
        }
    }

    .hero-slider {
        .hero-slider-wrapper {
            .slick-slide {
                margin: 0 20px;
                max-width: 250px;
            }

            .slick-list {
                margin: 0 -20px;
            }

            > div {
                padding-right: 0;
            }
        }
    }
}

@include media-breakpoint-only(xl) {
    .hero {
        .hero-video-wrapper {
            height: 686px;
        }

        .card-product-hero {
            padding: 20px 50px;

            .card-product-hero-img {
                padding: 34px 0;
            }

            &:before {
                top: 10px;
                height: 200px;
                width: 100px;
            }
        }
    }

    .hero-slider {
        .hero-slider-wrapper {
            width: 85%;
            margin-left: 10.2%;

            .slick-slide {
                margin: 0 40px;
            }

            .slick-list {
                margin: 0 -40px;
            }
        }
    }
}

@include media-breakpoint-down(lg) {
    .hero-slider {
        .container {
            margin-left: 4.5%;
            max-width: calc(95.5% - 16px);
        }
    }
    .hero-content {
        bottom: 0;
        padding: 40px 0;

        h4 {
            font-size: 14px;
            line-height: 21px;
            letter-spacing: -0.14px;
        }

        h1 {
            font-size: 25px;
            line-height: 34px;
            letter-spacing: -0.25px;
        }
    }

    .hero-slider {
        .hero-slider-wrapper {
            .slick-list {
                margin: 0;
            }
        }
    }
}

@include media-breakpoint-only(lg) {
    .hero {
        .hero-video-wrapper {
            height: 556px;
        }

        .card-product-hero {
            padding: 20px 50px;

            .card-product-hero-img {
                padding: 15px 0;

                img {
                    max-height: 106px;
                }
            }

            &:before {
                top: 10px;
                height: 100px;
                width: 70px;
                left: 80px;
            }
        }
    }

    .hero-content {
        bottom: 50%;
        transform: translateY(50%);
    }
}

@include media-breakpoint-down(md) {
    .hero {
        .hero-video-wrapper {
            height: 350px;
        }
    }
}

@include media-breakpoint-down(sm) {
    .hero-content {
        top: 50%;
        bottom: auto;
        transform: translateY(-50%);
    }
}