.gallery {
	overflow: hidden;

	.gallery-wrapper {
	    .gallery-img {
	    	position: relative;
	    	height: 250px;

	    	&:after {
	    		content: '';
	    		position: absolute;
	    		height: 100%;
	    		left: 0;
	    		right: 0;
	    		background: transparent;
	    		transition: 0.2s background;
	    	}

	    	&:hover {
	    		&:after {
	    			background: $primary;
	    		}
	    	}

			img {
			    width:100%;
			    height: 100%;
			    object-fit: cover;
			}
		}
	}
}

@include media-breakpoint-up(lg) {
	.gallery {
		.row {
			width: 90%;
			margin-left: auto;

			>div:nth-child(4) {
			  	margin-left: -190px;
			}
			>div:nth-child(7) {
			  	margin-left: -383px;
			}
		}

		.gallery-wrapper {
		    .gallery-img {
				height: 700px;
			    overflow: hidden;
			    transform: skewX(-15deg);

			    img {
					transform: skewX(15deg);
				    width:140%;
				    margin:0 -20%;
				}
			}
		}
	}
}

@include media-breakpoint-only(xl) {
	.gallery {
		.row {
			>div:nth-child(4) {
			  	margin-left: -147px;
			}
			>div:nth-child(7) {
			  	margin-left: -295px;
			}
		}

		.gallery-wrapper {
		    .gallery-img {
				height: 540px;
			}
		}
	}
}

@include media-breakpoint-only(lg) {
	.gallery {
		.row {
			>div:nth-child(4) {
			  	margin-left: -93px;
			}
			>div:nth-child(7) {
			  	margin-left: -185px;
			}
		}

		.gallery-wrapper {
		    .gallery-img {
				height: 332px;
			}
		}
	}
}

@include media-breakpoint-only(md) {
	.gallery {
		.container-fluid {
			max-width: 980px;
		}
	}
}

@include media-breakpoint-only(sm) {
	.gallery {
		.container-fluid {
			max-width: 704px;

			.gallery-wrapper {
			    .gallery-img {
					height: 460px;
				}
			}
		}
	}
}

@include media-breakpoint-down(sm) {
	.gallery {
		.gallery-wrapper {
		    .gallery-img {
				height: 230px;
			}
		}
	}
}